@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@200&display=swap);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Nunito", sans-serif;
}

body {
  background-color: whitesmoke;
}

h1 {
  margin: 32px 0;
  color: #fff;
  font-size: 24px;
}

h3 {
  margin: auto;
  color: rgb(66, 171, 231);
  font-size: 20px;
}

.wrap {
  margin: auto;
  padding: 5%;
}

.wrap-wrap {
  margin: auto;
  margin-bottom: 3%;
}

/* .about-title {
  background-color: rgba(0, 0, 0, 0.527);
} */

.flex-container {
  display: flex;
  flex-wrap: wrap;
  font-size: 22px;
  text-align: center;
  justify-content: center;
  align-items: center;
}

.flex-item-left {
  background-color: rgba(0, 0, 0, 0.527);
  color: white;
  padding: 10px;
  flex: 50% 1;
}

.flex-item-right {
  padding-left: 10%;
  padding-right: 10%;
  flex: 50% 1;
}

/* Responsive layout - makes a one column-layout instead of a two-column layout */
@media (max-width: 800px) {
  .flex-item-right,
  .flex-item-left {
    flex: 100% 1;
  }
}

a {
  color: #4db5ff;
  transition: all 400ms ease;
  text-decoration: none;
}

a:hover {
  color: #ffffff;
}

.title {
  color: #ffffff;
}

.btn {
  width: -webkit-max-content;
  width: max-content;
  display: inline-block;
  color: white;
  padding: 0.75rem 1.2rem;
  border-radius: 0.4rem;
  cursor: pointer;
  border: 1px solid rgb(155, 150, 150);
  transition: all 400ms ease;
}

.btn:hover {
  background: #ffffff;
  color: rgb(59, 58, 58);
  border-color: transparent;
}

.btn-primary {
  background: rgb(155, 150, 150);
  color: white;
}

.project-image {
  display: block;
  width: 100%;
  object-fit: cover;
}

.githubRepo_container {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 2.5rem;
  gap: 2.5rem;
  margin: 6%;
}

.githubRepo_item {
  background: rgb(59, 58, 58);
  padding: 1.2rem;
  border-radius: 2rem;
  border: 1px solid transparent;
  transition: all 400ms ease;
}

.githubRepo_item:hover {
  background-color: rgba(77, 181, 255, 0.4);
  background: transparent;
  border: 1px solid rgb(59, 58, 58);
}

.gitProject_item_image {
  border-radius: 1.5rem;
  overflow: hidden;
  height: 300px;
}

.githubRepo_item h3 {
  margin: 1.2rem 0 2rem;
}

.githubRepo_item_cta {
  display: flex;
  grid-gap: 1rem;
  gap: 1rem;
  margin-bottom: 1rem;
}

/* ==================== MEDIA QUERIES (Medium DEVICES) ==================== */
@media screen and (max-width: 1024px) {
  .githubRepo_container {
    grid-template-columns: 1fr 1fr;
    grid-gap: 1.2rem;
    gap: 1.2rem;
  }
}

/* ==================== MEDIA QUERIES (Small DEVICES) ==================== */
@media screen and (max-width: 600px) {
  .githubRepo_container {
    grid-template-columns: 1fr;
    grid-gap: 1rem;
    gap: 1rem;
  }
}

.nav__list {
  list-style-type: none;
  margin-top: 0.5rem;
  margin-right: 4rem;
  padding: 0;
  overflow: hidden;
}

.nav__list-home {
  float: left;
  margin-right: 1rem;
  margin-left: 1rem;
}

.nav__list-item {
  float: right;
  margin-right: 1rem;
  margin-left: 1rem;
}

.link.link--nav {
  display: block;
  color: white;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
  font-size: 25px;
}

.app .nav__hamburger {
  display: none;
}

.nav__theme {
  margin-top: 0.4em;
}

.hamburger {
  display: none;
  color: white;
}

@media (max-width: 600px) {
  .nav__list {
    display: none;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: fixed;
    inset: 0;
    width: 100%;
    height: 100%;
    z-index: 2;
  }

  .nav__list-item {
    margin: 0.5em 0;
  }

  .topnav .icon {
    background: black;
    display: block;
    position: absolute;
    right: 0;
    top: 0;
  }
}

@media screen and (min-width: 599px) {
  .me {
    width: 220px;
    height: 260px;
  }
}
@media screen and (max-width: 599px) {
  .me {
    width: 100px;
    height: 100px;
  }
}

.home {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  align-items: center;
  height: calc(100vh + 63px);
  z-index: 1000;
}
.greeting {
  pointer-events: none;
  margin: -63px 20px 0 20px;
}
.hide {
  display: none;
}
.me {
  border-radius: 50%;
  border: 0.2rem solid white;
}
.greeting-text {
  margin: 5px;
}
.name {
  color: white;
}
.scroll-down-element {
  display: inline-block;
}

.wrapper {
  margin: auto;
  width: 30%;
  padding-bottom: 6%;
}

.btn-container {
  position: relative;
}

.dl-btn {
  display: inline-block;
  border-radius: 4px;
  background-color: rgb(59, 58, 58);
  border: none;
  color: #ffffff;
  text-align: center;
  font-size: 28px;
  padding: 10px;
  width: 200px;
  transition: all 0.5s;
  cursor: pointer;
  position: absolute;
  margin: 0;
  -webkit-transform: translate(100%, -150%);
          transform: translate(100%, -150%);
}

.dl-btn span {
  cursor: pointer;
  display: inline-block;
  position: relative;
  transition: 0.5s;
}

.dl-btn span:after {
  content: "\00bb";
  position: absolute;
  opacity: 0;
  top: 0;
  right: -20px;
  transition: 0.5s;
}

.dl-btn:hover span {
  padding-right: 25px;
}

.dl-btn:hover span:after {
  opacity: 1;
  right: 0;
}

@media screen and (max-width: 1500px) {
  .wrapper {
    width: 50%;
  }
}

@media screen and (max-width: 992px) {
  .wrapper {
    width: 70%;
  }
}

@media screen and (max-width: 730px) {
  .wrapper {
    width: 85%;
  }
}

@media screen and (max-width: 600px) {
  .wrapper {
    width: 100%;
  }
}

.contact-header {
  color: black;
}

.outer-wrap {
  margin: auto;
  width: 70%;
  padding-top: 10%;
  padding-bottom: 10%;
}

.contact-wrapper {
  border-radius: 5px;
  background-color: #f2f2f2;
  padding: 20px;
  width: 40%;
  margin: auto;
}

input[type="text"],
select,
textarea {
  width: 100%;
  padding: 12px;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
  margin-top: 6px;
  margin-bottom: 16px;
  resize: vertical;
}

.contact-btn {
  background-color: rgb(59, 58, 58);
  color: white;
  padding: 12px 20px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.contact-btn:hover {
  background-color: black;
}

@media screen and (max-width: 992px) {
  .outer-wrap {
    width: 80%;
    padding-top: 10%;
  }
}

@media screen and (max-width: 775px) {
  .outer-wrap {
    width: 90%;
    padding-top: 10%;
  }
}

@media screen and (max-width: 660px) {
  .outer-wrap {
    width: 100%;
    padding-top: 10%;
  }
}

.icon-bar {
  width: 100%;
  background-color: rgb(59, 58, 58);
  overflow: auto;
  margin: auto;
  border-radius: 4px;
  position: fixed;
  left: 0;
  bottom: 0;
}

.icon-bar a {
  float: left;
  width: 33%;
  text-align: center;
  padding: 10px 0;
  transition: all 0.3s ease;
  color: white;
  font-size: 32px;
  margin: auto;
}

.icon-bar a:hover {
  background-color: #000;
}

